@use '../util' as *;

h1,
h2,
h3 {
	margin-top: 0;
	
}

a,
a:visited,
a:active {
	text-decoration: none;
}

h1,
h2,
h3 {
    //reset browser's default adding margin 
      margin-top: 0;
	  color: var(--h-col);
	  max-width: 73rem;
	  letter-spacing: 0.075rem;
}

h1 {
	// font-size: 3.2rem;
	font-size:clamp(3.2rem, 1.5vw, 4.6rem);
	font-weight: normal;
	// letter-spacing: 0.005rem;
	color: var(--hli-col);

}


p {
	margin: 0;
	// font-size: 1.6rem;
	font-size:clamp(1.6rem, 1.5vw, 2rem);

    color: var(--p-gray);
	// line-height: 2.6rem;
	letter-spacing: 0.03rem;
	
}




li {
    color: var(--h-col);
}
	
